          <div class="work-timeline__calendar"
            :data-st-date="`${timeLine[0].joinDate.year}.${timeLine[0].joinDate.month}`"
            :data-current-date="`${today.getFullYear()}.${today.getMonth()+1}`">
            <div
              class="work-timeline__bar"
              :data-total-month="workTotalMonth">
              <div
                v-for="(item, tIndex) in timeLine"
                :key="tIndex"
                :data-month="workTime(item.joinDate.year, item.joinDate.month, item.leaveDate.year, item.leaveDate.month)"
                :data-company-name="item.company"
                :style="{
                  width: !isMobile ? `${workMonth(item.joinDate.year, item.joinDate.month, item.leaveDate.year, item.leaveDate.month)/workTotalMonth*100}%`: 'auto',
                  marginLeft: (!isMobile && tIndex === 1) ? `${6/workTotalMonth*100}%` : '0px',
                  marginRight: (!isMobile && tIndex !== timeLine.length - 1 && tIndex !== 0) ? `${0.5/workTotalMonth*100}%` : '0px',
                }"
                class="work-timeline__progress"></div>
            </div>
          </div>
          <ol class="work-timeline__list">
            <li class="work-timeline__item" v-for="(item, tIndex) in timeLine" :key="tIndex">
              <div class="work-status__area">
                <strong>
                  <span>{{ `${item.joinDate.year}.${item.joinDate.month}` }}</span> ~
                  <span v-if="item.isLeave">{{ `${item.leaveDate.year}.${item.leaveDate.month}` }}</span>
                </strong>
                <div class="work-term__area">
                  <span class="work__badge work__badge--term">{{ workTime(item.joinDate.year, item.joinDate.month, item.leaveDate.year, item.leaveDate.month) }}</span>
                  <span v-if="!item.isLeave" class="work__badge work__badge--in-service">재직중</span>
                </div>
              </div>
              <div class="work-detail">
                <div class="company__area">
