                      class="main-task__item"
                      v-for="(taskItem, tIndex) in item.mainTask"
                      :key="tIndex">{{ taskItem }}</li>
                  </ul>
                </div>
                <div class="main-skill">
                  <h4 class="tit-h4">주요 스킬</h4>
                  <div class="work-skill">
