<script>
export default {
  name: 'Main',
  data () {
    return {
      timeLine: [
        {
          company: '(주) 시도우',
          position: 'IDA 사업부 웹 퍼블리셔 (연구원)',
          joinDate: {
            year: 2012,
            month: 11
          },
          leaveDate: {
            year: 2014,
            month: 11
          },
          isLeave: true,
          mainTask: [
            '웹 접근성 심사를 통한 인증마크 획득',
            '크로스브라우징 대응 (IE 7)',
            'PC, Mobile, 반응형 서비스 구축'
          ],
          mainSkill: ['HTML', 'HTML5', 'CSS', 'jQuery', 'Web Accessibility', 'Cross Browsing (IE 7+)', 'Responsive Web']
        },
        {
          company: '(주) 이라이즈',
          position: '퍼블리싱팀 웹 퍼블리셔 (대리)',
          joinDate: {
            year: 2015,
            month: 7
          },
          leaveDate: {
            year: 2015,
            month: 12
          },
          isLeave: true,
          mainTask: [
            '한화 갤러리아 관련 사이트 운영 업무',
            '크리스마스, 추석과 같은 모바일 기프트북 제작'
          ],
          mainSkill: ['HTML5', 'CSS3', 'jQuery', 'Responsive Web']
        },
        {
          company: '(주) 에스티유니타스',
          position: 'ST Tech본부 마크업팀 웹 퍼블리셔 (CD)',
          joinDate: {
            year: 2016,
            month: 1
          },
          leaveDate: {
            year: 2020,
            month: 4
          },
          isLeave: true,
          mainTask: [
            '커넥츠 앱 내 채팅 서비스 웹앱 작업',
            '취업개발팀 담당 단기(취업, 공기업, 자격증, 금융, 공인, 주택) 서비스 운영',
            '신사업(스콜레, 로티플) 관련 플랫폼 신규 구축 및 운영',
            '신입 퍼블리셔 대상 커리큘럼 작성 및 마크업 교육'
          ],
          mainSkill: ['Vue.js', 'Javascript', 'jQuery', 'HTML', 'HTML5', 'CSS', 'CSS3', 'SCSS', 'Cross Browsing (IE 7+)', 'Responsive Web']
        },
        {
          company: '(주) 브랜디',
          position: 'LABs 풀필먼트개발팀 프론트엔드 개발자 (B3)',
          joinDate: {
            year: 2020,
            month: 4
          },
          isLeave: false,
          mainTask: [
            '신규 프로젝트(T)와 관련 어드민 및 웹앱 화면 개발작업',
            '브랜디 내 반응형 웹 서비스 운영 및 리뉴얼',
            'Google Lighthouse를 활용한 Best Parctice, Accessibility, SEO 개선',
            '기존 브랜디 웹 서비스 내 레거시 코드에 대한 코드 리펙토링 작업 진행'
          ],
          mainSkill: ['Vue.js', 'Javascript', 'HTML5', 'CSS3', 'SCSS', 'Cross Browsing (IE 10+)', 'Web Accessibility', 'Responsive Web', 'SEO']
        }
      ]
    }
  },
  methods: {
    // totalWorkTime: function () {
    //   let total = 0
    //   for (let x of this.timeLine) {
    //     total += this.workDate(x.joinDate.year, x.joinDate.month, x.leaveDate.year, x.leaveDate.month)
    //   }
    // },
    workMonth: function (jYear, jMonth, lYear, lMonth) {
      const stDate = new Date(jYear, jMonth)
      const endDate = new Date(lYear, lMonth)
      const diffDate = endDate.getTime() - stDate.getTime()
      const lapseDate = diffDate / (1000 * 60 * 60 * 24)

      return Math.floor(lapseDate / 30) + 1
    },
    workTime: function (jYear, jMonth, lYear, lMonth) {
      let month, year
      const stDate = new Date(jYear, jMonth)
      const endDate = new Date(lYear, lMonth)
      const diffDate = endDate.getTime() - stDate.getTime()

      const lapseDate = diffDate / (1000 * 60 * 60 * 24)

      if (lapseDate < 30) {
        return `0개월`
      } else if (lapseDate < 365) {
        month = Math.floor(lapseDate / 30) + 1
        if (month === 12) {
          return `1년`
        } else {
          return `${month}개월`
        }
      } else {
        year = Math.floor(lapseDate / 365)
        month = Math.floor((lapseDate - (year * 365)) / 30) + 1
        if (month === 0) {
          return `${year}년`
        } else {
          return `${year}년 ${month}개월`
        }
      }
      // // return `${parseInt((endDate - stDate) / cYear)}년  ${parseInt((endDate - stDate) / cMonth)}개월`
    },
    skillBadgeType: function (skillName) {
      let className = 'work-skill__badge'
      switch (skillName) {
        case 'Javascript':
          return `${className}--js`
        case 'Vue.js':
          return `${className}--vue`
        case 'CSS':
        case 'CSS3':
          return `${className}--css`
        case 'SCSS':
        case 'SASS':
          return `${className}--scss`
        case 'HTML':
        case 'HTML5':
          return `${className}--html`
      }
    },
    // companyClassName: function (company) {
    //   let className = 'work-timeline__progress'
    //   switch (company) {
    //     case '(Javascript)':
    //       return `${className}--js`
    //     case 'Vue.js':
    //       return `${className}--vue`
    //     case 'CSS':
    //     case 'CSS3':
    //       return `${className}--css`
    //     case 'SCSS':
    //     case 'SASS':
    //       return `${className}--scss`
    //     case 'HTML':
    //     case 'HTML5':
    //       return `${className}--html`
    //   }
    // },
    addLeaveDate: function () {
      for (let x of this.timeLine) {
        if (!x.hasOwnProperty('leaveDate')) {
          x.leaveDate = {}
          x.leaveDate['year'] = this.today.getFullYear()
          x.leaveDate['month'] = this.today.getMonth()
        }
      }
    }
  },
  computed: {
    today: function () {
      return new Date()
    },
    workTotalMonth: function () {
      return this.workMonth(2012, 11, this.today.getFullYear(), this.today.getMonth())
    },
    totalCareer: function () {
      return this.workTime(2013, 5, this.today.getFullYear(), this.today.getMonth())
    }
  },
  created () {
    this.addLeaveDate()
  }
}
</script>

<style lang="scss" scoped>
#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
#header {
  // flex: 0 0 auto;
}
#main {
  flex: 1 1 auto;
  padding: 20px 0;
}
.inner {
  width: calc(100% - 32px);
  max-width: 1168px;
  padding: 0 16px;
  margin: 0 auto;
}
.tit-h1, .tit-h2, .tit-h3, .tit-h4 {font-weight: 700}
.tit-h1 {
  font-size: 36px;
}
.tit-h2 {
  font-size: 28px;
  margin-bottom: 12px;
  .tit-sub {
    font-weight: 400;
    font-size: 18px;
  }
}
.tit-h3 {
  font-size: 22px;
}
.tit-h4 {
  font-size: 18px;
}
.work-timeline__progress {
  flex: 0 1 auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  &:nth-child(n+2) {
    margin-top: 10px;
  }
  &:before {
    flex: 0 0 auto;
    overflow: hidden;
    display: flex;
    align-items: center;
    // height: 20px;
    padding: 0 5px;
    font-size: 12px;
    color: #fff;
    line-height: 20px;
    background-color: #333;
    text-overflow: ellipsis;
    white-space: nowrap;
    content: attr(data-company-name) ' (' attr(data-month) ')';
  }
  .work-skill {
    display: none;
  }
}
.work-timeline__list {
  display: flex;
  flex-direction: column-reverse;
}
.work-timeline__item {
  flex: 1 1 auto;
}
.main-skill {
  .work-skill__badge {
    margin: 0 5px 5px 0;
  }
}
.work-skill {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.work-skill__badge {
  @mixin badgeColor($bColor: #000) {
    border-color: $bColor;
    background-color: $bColor;
  }
  overflow: hidden;
  flex: 0 0 auto;
  display: block;
  max-width: 100%;
  padding: 0 8px;
  // margin: 0 5px 5px 0;
  font-weight: 700;
  font-size: 14px;
  color: #fff;
  line-height: 20px;
  white-space: nowrap;
  text-overflow: ellipsis;
  @include badgeColor(#444);
  border-radius: 2px;
  // &--html {
  //   color: #fff;
  //   @include badgeColor(#d63a14)
  // }
  &--scss {
    color: #fff;
    @include badgeColor(#cb6899);
  }
  // &--css {
  //   color: #fff;
  //   @include badgeColor(#264de4)
  // }
  &--js {
    color: #000;
    @include badgeColor(#f6e017);
  }
  &--vue {
    color: #fff;
    @include badgeColor(#42b983);
  }
}
@media screen and (min-width: 769px) {
  .work-timeline__calendar {
    position: relative;
    display: flex;
    padding: 10px 0 20px;
    border: 1px solid #333;
    border-width: 0 1px;
    &:before,
    &:after {
      display: block;
      position: absolute;
      bottom: 0;
    }
    &:before {
      left: 5px;
      content: attr(data-st-date);
    }
    &:after {
      right: 5px;
      content: attr(data-current-date);
    }
  }
  .work-timeline__bar {
    flex: 1 1 auto;
    display: flex;
    flex-direction: row;
    position: relative;
    &:before {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #dedede;
      content: "";
    }
  }
  .work-timeline__progress {
    position: relative;
    flex: 1 1 auto;
    &:before {
      height: 100%;
      padding: 5px;
      font-size: 14px;
      white-space: normal;
      word-break: keep-all;
      word-wrap: break-word;
      box-sizing: border-box;
    }
    &:nth-of-type(n+2) {
      margin-top: 0;
    }
    .work-skill {
      flex: 0 0 auto;
      display: flex;
      flex-direction: column;
    }
    .work-skill__badge {
      margin: 5px 5px 0 0;
    }
  }
}
</style>



